import { NewAffiliateRequestComponent } from './affiliate-core/new-affiliate-request/new-affiliate-request.component';
import { ReferredUsersComponent } from './affiliate-core/referred-users/referred-users.component';
import { AffiliateRequestComponent } from './affiliate-core/affiliate-request/affiliate-request.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AffiliateCoreComponent } from './affiliate-core/affiliate-core.component';


const routes: Routes = [
  {
    path: '',
    component: AffiliateCoreComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        redirectTo: 'affiliate-request'
      },
      {
        path: 'affiliate-request',
        component: AffiliateRequestComponent,
        data: {title: 'Affiliate Request', breadcrumb: 'Affiliate Request'}
      },
      {
        path: 'new-affiliate-request',
        component: NewAffiliateRequestComponent,
        data: {title: 'Affiliate Request', breadcrumb: 'New Affiliate Request'}
      },
      {
        path: 'referred-users-list',
        component: ReferredUsersComponent,
        data: {title: 'Referred Users', breadcrumb: 'Referred Users list'}
      },
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class AffiliateRoutingModule { }
