import { NotificationService } from './../../../../../shared/services/notification.service';
import { Router, ActivatedRoute } from '@angular/router';
import { AffiliateService } from './../../../../../shared/services/affiliate.service';
import { Subscription } from 'rxjs';
import { GlobalService } from './../../../../../shared/services/global.service';
import { HeaderService } from './../../../../../shared/services/header.service';
import { NgForm } from '@angular/forms';
import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-new-affiliate-request',
  templateUrl: './new-affiliate-request.component.html',
  styleUrls: ['./new-affiliate-request.component.scss']
})
export class NewAffiliateRequestComponent implements OnInit, OnDestroy {
  LoggedParams: any;
  isLoading = false;
  messageToDisplay: string;
  ErrorMsqBlock = false;
  successMsqBlock = false;
  liveAccountData: any[] = [];
  subscription1: Subscription;
  subscription2: Subscription;
  subscription3: Subscription;
  selectedSocialSite: any;
  isAffiliateUpdate = false;
  @ViewChild('formData', {static: false}) affiliateSignup: NgForm;
  constructor(
    private headerService: HeaderService,
    private globalService: GlobalService,
    private affiliateService: AffiliateService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private notificationService: NotificationService
  ) {
    this.subscription3 = this.affiliateService.castUpdateAffiliateStatus.subscribe(
      status => (this.isAffiliateUpdate = status)
    );
    this.subscription1 = this.headerService.castLoggedUserDetail.subscribe(
      resData => (this.LoggedParams = resData)
    );
    console.log(this.isAffiliateUpdate);
   }

  ngOnInit(): void {
    this.getProfileAccounts();
  }
  getProfileAccounts() {
    const params = {
      ProfileId: this.LoggedParams.sProfile.sMasterId,
      LoginId: this.LoggedParams.sProfile.sMasterId
    };
    this.subscription2 = this.globalService.GET_PROFILE_ACCOUNTS(params).subscribe(
      data => {
        this.liveAccountData = data.lstAccount.map( acc => {
          if (acc) {
            return Object.assign(acc.sUserInfo, acc.sAccountPlan);
          }
        });
        console.log( this.liveAccountData);
      }
    );
  }
  onSubmit() {
    this.isLoading = true;
    const params = {
        Affiliateid: 0,
        ProfileId: this.LoggedParams.sProfile.sMasterId,
        Account: this.affiliateSignup.value.accountnumber,
        PkgId: 0,
        PkgName: 0,
        Name: this.LoggedParams.sProfile.uPersonalInfo.First,
        Email: this.LoggedParams.sProfile.uContactInfo.Email,
        Company: this.affiliateSignup.value.cname,
        Company_Addr: this.affiliateSignup.value.caddress,
        uStatus: this.LoggedParams.sProfile.uStatus,
        SocialInfo: {
          FaceBook: this.affiliateSignup.value.FaceBook,
          GitHub:  ' ',
          GooglePlus: ' ',
          LinkedIn: this.affiliateSignup.value.LinkedIn,
          PInterest: ' ',
          RSSLink: ' ',
          Skype: ' ',
          Telegram: ' ',
          Twitter: this.affiliateSignup.value.Twitter,
          Website: this.affiliateSignup.value.Website,
          WhatsApp: ' ',
          YouTube:  this.affiliateSignup.value.YouTube,
        },
        ReferCode: 0,
      };
    console.log(params);
    this.globalService.makeAffReq(params).subscribe(data => {
      if (data.Result === true) {
        this.isLoading = false;
        this.notificationService.showSuccess('Your request has been generated Successfully. Please wait for admin approval.', 'Success');
        // this.onSuccessMsqDisplay('Affiliate Profile created Successfully');
        // this.autoCloseMsqBlock();
        this.router.navigate(['../affiliate-request'], {relativeTo: this.activatedRoute});
      } else {
        this.onErrorMsqDisplay('Faild to Create Affiliate Profile');
        this.notificationService.showError('Faild to Create Affiliate Profile.', 'Error');
        this.isLoading = false;
        // this.autoCloseMsqBlock();
      }
      });
    this.isLoading = false;
  }
  autoCloseMsqBlock() {
    setTimeout(() => {
      this.successMsqBlock = false;
      this.ErrorMsqBlock = false;
      this.messageToDisplay = '';
    }, 10000);
  }
  onCloseMsqBlock() {
    this.successMsqBlock = false;
    this.ErrorMsqBlock = false;
    this.messageToDisplay = '';
  }
  onSuccessMsqDisplay(msq: string) {
    this.messageToDisplay = '';
    this.ErrorMsqBlock = false;
    this.successMsqBlock = true;
    this.messageToDisplay = msq;
  }
  onErrorMsqDisplay(msq: string) {
    this.messageToDisplay = '';
    this.successMsqBlock = false;
    this.ErrorMsqBlock = true;
    this.messageToDisplay = msq;
  }
  ngOnDestroy() {
    this.subscription1.unsubscribe();
    this.subscription2.unsubscribe();
    this.subscription3.unsubscribe();
  }

}
