import { GlobalService } from './../../../../../shared/services/global.service';
import { HeaderService } from './../../../../../shared/services/header.service';
import { Subscription } from 'rxjs';
import { OrderPipe } from 'ngx-order-pipe';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-referred-users',
  templateUrl: './referred-users.component.html',
  styleUrls: ['./referred-users.component.scss']
})
export class ReferredUsersComponent implements OnInit {
  public isLoading = false;
  public LoggedParams: any;
  public AffiliatedUsersProfile: any;
  public AffiliatedUsersList: any [];
  public subscription1: Subscription;

   // pagination Setting
   public TotalCount = 0;
   public cat;
   public searchText: any; // Search Text property is used to filter text in table
   public p: any = 1;          // Current page in pagination
   public pageSize = 5;  // Table Rows Perpage property
   public sortedData: any[]; // Sorted Product list array
   public order = '';     // Order property to sort coloum of table
   public reverse = true; // Reverse property is used to sort table coloum
   public setParams: any;
   public IsDataNotFound = false;
  constructor(
    private orderPipe: OrderPipe,
    private headerService: HeaderService,
    private globalService: GlobalService
  ) {
    this.sortedData = orderPipe.transform(this.AffiliatedUsersList, this.order);
  }
  setOrder(value: string) {
    if (this.order === value) {
      this.reverse = !this.reverse;
    }
    this.order = value;
  }

  ngOnInit(): void {
    this.subscription1 = this.headerService.castLoggedUserDetail.subscribe(
      resData => (this.LoggedParams = resData)
    );
    // this.getAffilateStatus(this.p, this.pageSize);
    this.getAffilateStatus(1, this.cat);
  }
  getAffilateStatus(indexval, countVal) {
    this.isLoading = true;
    if (countVal) {
      this.pageSize = indexval;
      this.p = 1;
      this.setParams = {
        Affiliate: this.LoggedParams.sProfile.sMasterId,
        DealerID: this.LoggedParams.sProfile.sDealerId,
        Count: this.p,
        Index: this.pageSize
      };
    } else {
      this.p = indexval;
      this.setParams = {
        Affiliate: this.LoggedParams.sProfile.sMasterId,
        DealerID: this.LoggedParams.sProfile.sDealerId,
        Count:  (this.p - 1) * this.pageSize + 1,
        Index: this.p * this.pageSize
      };
    }
    this.subscription1 = this.globalService.getAffProfiles(this.setParams).subscribe(
      data => {
        if (data.length !== 0) {
          this.IsDataNotFound = false;
          this.isLoading = false;
          this.TotalCount = data.TotalCount;
          this.AffiliatedUsersProfile = data;
          this.AffiliatedUsersList = this.AffiliatedUsersProfile.lstProfile;
        } else {
          this.IsDataNotFound = true;
          this.isLoading = false;
        }
       }
    );
  }

}
