import { Router, ActivatedRoute } from '@angular/router';
import { AffiliateService } from './../../../../../shared/services/affiliate.service';
import { GlobalService } from './../../../../../shared/services/global.service';
import { HeaderService } from './../../../../../shared/services/header.service';
import { Subscription } from 'rxjs';
import { Component, OnInit, OnDestroy } from '@angular/core';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-affiliate-request',
  templateUrl: './affiliate-request.component.html',
  styleUrls: ['./affiliate-request.component.scss']
})
export class AffiliateRequestComponent implements OnInit, OnDestroy {

  public isLoading = false;
  public walletId: string;
  public dealerId: string;
  public LoggedParams: any;
  public AffiliateStatus: any[];
  public isAffiliateAvaliable = true;
  public subscription1: Subscription;
  public editAffiliate = false;

  constructor(
    private headerService: HeaderService,
    private globalService: GlobalService,
    private affiliateService: AffiliateService,
    private router: Router,
    private activatedRoute: ActivatedRoute,
  ) { }

  ngOnInit(): void {
    this.isLoading = true;
    this.subscription1 = this.headerService.castLoggedUserDetail.subscribe(
      resData => (this.LoggedParams = resData)
    );
    this.getAffilateStatus();
  }
  getAffilateStatus() {
    const params = {
      ProfileId: this.LoggedParams.sProfile.sMasterId,
    };
    this.subscription1 = this.globalService.getAffStatus(params).subscribe(
      data => {
        if (data.length !== 0) {
          this.AffiliateStatus = data;
          this.isAffiliateAvaliable = true;
          this.isLoading = false;
        } else {
          this.AffiliateStatus = data;
          this.isAffiliateAvaliable = false;
          this.router.navigate(['../new-affiliate-request'], {relativeTo: this.activatedRoute});
          this.isLoading = false;
        }
        console.log(data);
       }
    );
  }
  onEditAffiliate() {
    this.editAffiliate = true;
    this.affiliateService.getupdateAffiliateStatus(true);
    this.affiliateService.getAffilateDataToUpdate(this.AffiliateStatus);
  }
  onDelete(selectedItem) {
    Swal.fire({
      title: 'Are you Sure!',
      text: 'Do you really want to delete these Affiliate? This process cannot undone',
      icon: 'error',
      showCancelButton: true,
      confirmButtonText: 'Delete',
      cancelButtonText: 'Cancel',
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
    }).then((result) => {
      if (result.value) {
        const params = {
          ProfileId: this.LoggedParams.sProfile.sMasterId,
          Account: selectedItem.Account
        };
        console.log(params);
        this.globalService.DELETE_AFFILIATE_PROFILE(params).subscribe(data => {
          console.log(data);
          this.getAffilateStatus();
        });
      }
    });
  }
  ngOnDestroy() {
    this.subscription1.unsubscribe();
  }

}
