import { AffiliateService } from './../../../../../shared/services/affiliate.service';
import { OrderPipe } from 'ngx-order-pipe';
import { GlobalService } from './../../../../../shared/services/global.service';
import { HeaderService } from './../../../../../shared/services/header.service';
import { Subscription } from 'rxjs';
import { Component, OnInit, ViewChild, OnDestroy } from '@angular/core';

@Component({
  selector: 'app-affiliate-request-details',
  templateUrl: './affiliate-request-details.component.html',
  styleUrls: ['./affiliate-request-details.component.scss']
})
export class AffiliateRequestDetailsComponent implements OnInit, OnDestroy {

  public isLoading: false;
  public walletId: string;
  public dealerId: string;
  public LoggedParams: any;
  public BannerDetail: any;
  public referCode: any;
  public subscription1: Subscription;
  public subscription2: Subscription;
  public affiliateLinkCode: any;
  public htmlBannerCode: any;
  constructor(
    private headerService: HeaderService,
    private globalService: GlobalService,
  ) {
  }
  ngOnInit(): void {
    this.subscription1 = this.headerService.castLoggedUserDetail.subscribe(
      resData => (this.LoggedParams = resData)
    );
    this.getBannerDetail();
    setTimeout(() => {
      this.affiliateLinkCode = `http://client.traderscabinet.com/tr_test/#/auth/signup/?affiliate=${this.referCode}`;
      // tslint:disable-next-line: max-line-length
      // this.htmlBannerCode = `<a href="" onClick="this.href='https://my.cabanacapitals.com/#/?affiliate=ft@txttt'"> <img src="http://207.180.199.14:8021/docs/P1563769743855/banner.jpeg" ></a>`;
     }, 1000);
  }
  getBannerDetail() {
    const params = {
      ProfileId: this.LoggedParams.sProfile.sMasterId,
    };
    this.subscription2 = this.globalService.getAflbanner(params).subscribe(
      data => {
        console.log(data);
        this.BannerDetail = data.lstBanner;
        this.referCode = data.ReferCode;
       }
    );
  }

  ngOnDestroy() {
    this.subscription1.unsubscribe();
    this.subscription2.unsubscribe();
  }

}
